.common-anchor {
  padding: 0.75em 1.5em;
  background: #34617a;
  border-radius: 0.3125em;
  font-size: 0.875em;
  color: #fff;
  display: inline-block;
  position: relative;
  font-weight: 600;
  z-index: 1;
  overflow: hidden;
}
.common-anchor::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  background: #e79035;
  height: 100%;
  width: 0%;
  transition: 0.5s;
  border-radius: 0.3125em;
  z-index: -1;
}
.common-anchor:hover {
  color: #ffff;
}
.common-anchor:hover::before {
  width: 100%;
}
.common-anchor-blue {
  padding: 0.75em 1.5em;
  background: #0792aa;
  border-radius: 0.3125em;
  font-size: 0.875em;
  color: #fff;
  display: inline-block;
  position: relative;
  z-index: 0;
  font-weight: 600;
}
.common-anchor-blue::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  background: #fff;
  height: 100%;
  width: 0%;
  transition: 0.5s;
  border-radius: 0.3125em;
}
.common-anchor-blue:hover {
  color: #34617a;
}
.common-anchor-blue:hover::before {
  width: 100%;
  z-index: -1;
}
