.modal-cover {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  transform: translateZ(0);
  background-color: rgba(0, 0, 0, 0.8);
}
.modal-area {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  /* padding: 2.5em 1.5em 1.5em 1.5em; */
  background-color: #ffffff;
  box-shadow: 0 0 10px 3px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  margin: auto;
}
@media screen and (min-width: 300px) {
  /* Center the Modal! */
  .modal-area {
    left: 50%;
    top: 50%;
    height: auto;
    transform: translate(-50%, -50%);
    max-width: 75em;
    margin: auto;

    max-height: calc(100% - 1em);
  }
}

@media (max-width: 767px) {
  .modal-area {
    max-width: 100%;
    width: 90%;
  }
}
.modal-area .about-us-right-img {
  padding: 2em;
}
